import React from 'react';
import { Icon, Tooltip, Modal } from 'antd';
import { inject, observer } from 'mobx-react'
import { withRouter } from 'react-router-dom'
import { withTranslation } from 'react-i18next';
import './style.scss';
import { host, wsget } from '../../ws';
import { storage, session, removeCookie } from '../../util/util';
import Languageselect from '../../common/Languageselect';
import event from '../../util/event';

const { confirm } = Modal;

@withRouter
@withTranslation()
@inject('login', 'photoalbum', 'rahmen', 'playlist', 'uploadFiles')
@observer
class HeaderBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      languageNav: false,
      selectUser: false,
      user_data: {}
    }
  }

  componentDidMount() {
    this.profile = event.on('setNickname', (res) => {
      this.props.login.getUser_detail();
    })

    this.avatar = event.on('avatar', (id) => {
      this.props.login.getUser_detail();
    })

    document.addEventListener("click", this.hide)
  }
  componentWillUnmount() {
    document.removeEventListener("click", this.hide);
    event.rm(this.profile)
    event.rm(this.avatar)
  }

  hide = () => {
    this.setState({ selectUser: false })
  }

  selectInfo = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    this.setState({ selectUser: !this.state.selectUser })
  }
  //跳转个人信息页
  goPersonalDetails = () => {
    this.props.history.push('/personal')
  }

  clear = () => {
    storage.remove('user');
    storage.remove('Z_H_U_DATA');
    session.remove('token');
    removeCookie('cookie');
    this.props.login.clearData();
    this.props.photoalbum.clearData();
    this.props.rahmen.clearData();
    this.props.playlist.clearData();
    this.props.uploadFiles.clearData();
    this.props.history.push('/login');
    wsget('user_logout', null);
  }

  //退出登录
  loginout = () => {
    let _this = this;
    confirm({
      title: this.props.t('loginout?'),
      onOk() {
        _this.clear();
      },
      onCancel() { },
    });

  }

  toClear = () => {
    this.props.history.push(`/clear`)
  }

  render() {
    const { t } = this.props;
    const { selectUser } = this.state;
    const { user_info } = this.props.login;

    return (
      <div className='headerbar'>
        <div className='bars'>
          <Languageselect />
          <div
            className={`select-user treeNodeUnselectable ${selectUser ? 'act-user' : null}`}
            onClick={this.selectInfo}
          >
            <span className='profile'>
              {user_info.profile &&
                <img
                  alt=''
                  src={`${host()}/uf/s/${user_info.profile.id}/${user_info.id}`} />}
            </span>
            <Tooltip
              placement="left"
              overlayClassName='tooltip'
              title={user_info.nickname ? user_info.nickname : user_info.name}>
              <span className='user-name ellipsis'>{user_info.name ? user_info.nickname ? user_info.nickname : user_info.name : t('Login_in')}</span>
            </Tooltip>
            <Icon className='B5C0D2' type="caret-down" />
            <div className='user-info'>
              <ul>
                {/* <li onClick={this.toClear}>{t('Cleanphotos')}</li> */}
                <li onClick={this.goPersonalDetails}>{t('account')}</li>
                <li onClick={this.loginout}>{t('Logout')}</li>
              </ul>
            </div>
          </div>
          {/* <div className='inform'>
            {user_data.unread && <span className='informNum'>{user_data.unread}</span>}

            <Icon
              className='B5C0D2'
              style={{ fontSize: '24px' }}
              type="bell"
              theme="filled"
            />
            <div className='inform-box'>
              <div className='inform-box-head'></div>
            </div>
          </div> */}
        </div>
      </div>
    );
  }
};

export default HeaderBar;
