import React from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import { message, Modal } from 'antd';
import { inject, observer } from 'mobx-react';
import { withTranslation } from 'react-i18next';
import './App.scss';
import event from './util/event'
import PrivateRoute from './router/PrivateRoute';
import Index from './page/index';
import LoadableComponent from './common/Tool/LoadableComponent';
import { os } from './util/util';
import Phone from './components/Phone';
import Abnormal from './components/Abnormal/index'
import { observable } from 'mobx';
import oneimg from './images/111.png'
import twoimg from './images/222.png'

const Login = LoadableComponent(() => import('./page/Login'))
const Register = LoadableComponent(() => import('./page/Register'))
const Perfectinformation = LoadableComponent(() => import('./components/Perfectinformation'))

message.config({
  top: 100,
  maxCount: 1,
});


class Tutorial extends React.PureComponent {
  render() {
    return (
      <div className='Tutorial'>
        <div className='Tutorial-immer'>
          <p>1、打开IE浏览器；</p>
          <p>2、浏览器右上角，依次点击图片红框内的按钮；如下图所示：</p>
          <div className='oneimg'>
            <img src={oneimg} alt="" />
          </div>
        </div>
        <div>
          <p>3、依次点击图片内红框内的按钮。如下图所示：</p>
          <div>
            <img src={twoimg} alt="" />
          </div>
        </div>
      </div>
    )
  }
}



@withTranslation()
@inject('login', 'playlist', 'photoalbum')
@observer
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      connecting: true,
      successlconnection: false,
      disconnect: false
    }
  }
  @observable connect = null;


  componentDidMount() {
    event.on('wsinit', (msg) => {
      if (msg === 1) {
        this.setState({ connecting: false, successlconnection: true, disconnect: false })
        this.connect = null;
        setTimeout(() => {
          this.setState({ visible: true, successlconnection: false })
        }, 3000);
      }
      if (msg === 2) {
        this.setState({ visible: false, connecting: false, successlconnection: false, disconnect: true })
        let u = navigator.userAgent;
        if (u.indexOf("Edge") > -1) {
          this.connect = 'Edge'
        }
        if (!!window.ActiveXObject || "ActiveXObject" in window) {
          this.connect = 'IE'
        }
      }
    })

    const OS = os();

    if (OS.isAndroid || OS.isPhone) {
      // 手机
      this.props.login.getos('phone')
    } else if (OS.isTablet) {
      // 平板
      this.props.login.getos('tab')
    } else if (OS.isPc) {
      // pc
      this.props.login.getos('pc')
    }
  }

  info = () => {
    Modal.info({
      content: <Tutorial />,
      icon: null,
      okText: 'close',
      width: '100%',
      centered: true,
      onOk() { },
    });
  }

  render() {
    const { t } = this.props;
    const { visible, connecting, successlconnection, disconnect } = this.state;
    const unit = os();
    if (unit.isAndroid || unit.isPhone) {
      return <Phone />
    } else {
      return (
        <div id='app'>
          {/* {this.connect && <div className='course'>
            <div className='course-inner'>
              服务器一直连接不上？ <span className='click' onClick={this.info}>点击这里</span> 或不使用IE浏览器浏览，建议使用谷歌浏览器和其他浏览器打开网站！
            </div>
            <span className='close'>X</span>
          </div>} */}
          <Router>
            <Switch>
              <Route exact path='/login' component={Login} />
              <Route exact path='/register' component={Register} />
              <PrivateRoute exact path='/perfectinformation/:id' component={Perfectinformation} />
              <PrivateRoute path='/' component={Index} />
              <Route component={Abnormal} />
            </Switch>

            <div id='message' style={visible ? { display: 'none' } : { display: 'block' }}>
              {connecting ? <span>{t('Connecting')}</span> : null}
              {successlconnection ? <span>{t('connection')}</span> : null}
              {disconnect ? <span>{t('reconnecting')}</span> : null}
            </div>
          </Router>
        </div>
      );
    }
  }
}

export default App;
