import { set, remove, observable, action } from 'mobx';
import { message } from 'antd';
import { wsget } from '../../ws';
import { fileSort } from '../../util/util';
import i18next from 'i18next';

class albumAdd {
  @observable list = null //相册列表
  @observable partlist = false;
  @observable detailList = [];
  @observable detail = false;
  @observable ftbtn = false;
  @observable name = null;
  @observable SelectImgs = [];
  @observable page = 0; //第一条数据
  @observable number = null;
  @observable hasMoreItems = true;
  @observable columnCount = null; //行数

  //加载相册列表
  @action getAlbum() {
    wsget('album_list', null).then((data) => {
      this.list = fileSort.update(observable(data));
    })
  }

  @action getDetail = (id, name) => {
    this.ftbtn = true;
    this.detail = true;
    this.name = name;
    this.SelectImgs = [];
    this.id = id;
  }
  @action getData = (getWH, getH) => {
    wsget('album_detail_mini', this.id).then((data) => {
      this.columnCount = getWH();
      this.number = data.length;
      this.pushData(data);
      getH();
      this.partlist = true;
    })
  }

  @action goback = () => {
    this.detail = false;
    this.ftbtn = false;
    this.SelectImgs = [];
    this.partlist = null;
  }

  @action checkclick = (id) => {
    const idx = this.detailList.findIndex(item => item.id === id);
    const list = this.detailList[idx];
    if (this.SelectImgs.length > this.number) {
      this.SelectImgs = this.SelectImgs.filter((item) => {
        return !item.true;
      })
    }
    if (list.check) {
      remove(list, 'check');
      let pos = this.SelectImgs.findIndex(item => item === list);
      if (pos > -1) {
        this.SelectImgs.remove(list)
      }
    } else {
      set(list, { check: true });
      this.SelectImgs.push(list);
    }
    if (this.SelectImgs.length === this.number) {
      this.detailList.forEach(el => {
        if (el.true) {
          this.SelectImgs.push(el);
        }
      });
    }
  }

  @action checkAll = () => {
    if (this.detailList.length === this.SelectImgs.length) {
      this.detailList.forEach(el => {
        remove(el, 'check');
      });
      this.SelectImgs = [];
    } else {
      this.SelectImgs = [];
      this.detailList.forEach(el => {
        this.SelectImgs.push(el);
        set(el, { check: true });
      });
    }
  }

  @action invert = () => {
    this.SelectImgs = [];
    this.detailList.forEach(el => {
      if (!el.true) {
        if (el.check) {
          remove(el, 'check');
        } else {
          set(el, { check: true });
          this.SelectImgs.push(el);
        }
      }
    })
    console.log(this.SelectImgs.slice());
  }

  @action handleOk = (cb, onOk) => {
    if (this.SelectImgs.length === 0) {
      message.error(i18next.t('Noselectedpic'));
      return;
    }
    let newarr = this.SelectImgs.filter(item => !item.true);
    this.ftbtn = false;
    this.detail = false;
    this.partlist = false;
    onOk(newarr, () => {
      this.SelectImgs = [];
    });
    cb();
  };

  @action handleCancel = (cb) => {
    this.detail = false;
    this.ftbtn = false;
    this.partlist = false;
    cb();
  }

  @action pushData = (list) => {
    if (list.length % this.columnCount !== 0) {
      this.fornum = this.columnCount - list.length % this.columnCount;
      for (let index = 0; index < this.fornum; index++) {
        list.push({ true: true });
      }
    }
    this.detailList = list;
  }

}

export default new albumAdd();