import React from 'react';
import i18next from 'i18next';

export function Footbtn(props) {
  const { disabled, handlecancel, handleconfirm, noframes } = props;
  return (
    <div className='dialog-handle'>
      {!noframes && <span
        className='cancel-btn'
        onClick={handlecancel}>{i18next.t('Cancel')}</span>}
      <span
        className={`confirm-btn ${disabled ? 'disabled-btn' : ''}`}
        onClick={handleconfirm}>{i18next.t('Determine')}</span>
    </div>
  )
}