import React from 'react';
import { Icon } from 'antd';
import { NavLink } from 'react-router-dom'
import { withTranslation } from 'react-i18next';
import './style.scss'

@withTranslation()
class SiderNav extends React.Component {
  state = {
    visible: false
  }

  componentDidMount() {
    document.addEventListener('click', this.hide)
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.hide);
  }

  show = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    this.setState({ visible: !this.state.visible })
  }

  hide = (e) => {
    this.setState({ visible: false })
  }

  render() {
    const { t } = this.props;
    const nav = [
      {
        title: t('homepage'),
        key: '/home'
      },
      {
        title: t('playlist'),
        key: '/playlist'
      },
      {
        title: t('cloudalbum'),
        key: '/album'
      },
      {
        title: t('photoframe'),
        key: '/rahmen'
      },
      // {
      //   title: t('Medialibrary'),
      //   key: '/medialibrary'
      // }
    ]
    return (
      <div className='nav-inner'>
        <div className='nav'>
          {
            nav.map((item) => {
              return (
                <NavLink
                  key={item.key}
                  to={item.key}
                >
                  <span>{item.title}</span>
                </NavLink>
              )
            })
          }
        </div>
        {/* <div className='menu-btn'>
          <Icon type="menu-unfold" />
        </div> */}


        <div className='menu-sidebar' style={this.state.visible ? { transform: 'translateX(275px)' } : null}>
          <div className='menu-list'>
            <ul>
              {
                nav.map((item) => {
                  return (
                    <li key={item.key}>
                      <NavLink
                        to={item.key}
                      >
                        <span>{item.title}</span>
                      </NavLink>
                    </li>
                  )
                })
              }
            </ul>
            <span className='menu-btn' onClick={this.show}>
              {this.state.visible ? <Icon type="menu-fold" /> : <Icon type="menu-unfold" />}
            </span>
          </div>
        </div>
      </div>
    )
  }
}

export default SiderNav;