import React, { Component } from 'react';
import { Icon } from 'antd';
import { withTranslation } from 'react-i18next';
import dayjs from 'dayjs';
import "./style.scss";
import { storage } from '../../util/util';
import { getLanguage } from '../../util/getLanguage';

@withTranslation()
class Languageselect extends Component {
  state = {
    languagelist: [
      { id: 2, language: 'English', abbreviation: 'en' }
    ],
    languageNav: false,
    languageinit: [],
  }
  componentDidMount() {
    let index = this.state.languagelist.findIndex((item) => {
      return item.abbreviation === getLanguage();
    })

    this.setState({ languageinit: this.state.languagelist[index] })
    document.addEventListener("click", this.hide)
  }
  componentWillUnmount() {
    document.removeEventListener("click", this.hide);
  }
  hide = () => {
    this.setState({ languageNav: false })
  }
  selectLanguage = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    this.setState({ languageNav: !this.state.languageNav })
  }
  chooseLanguage(e, index) {
    e.stopPropagation();
    this.setState({ languageinit: this.state.languagelist[index] }, () => {
      const { i18n } = this.props;
      storage.set('language', this.state.languageinit.abbreviation);
      i18n.changeLanguage(this.state.languageinit.abbreviation);
      if (this.state.languageinit.abbreviation === 'zh') {
        dayjs.locale('zh-cn');
      }
      if (this.state.languageinit.abbreviation === 'en') {
        dayjs.locale('en');
      }
    })
  }
  render() {
    const { languagelist, languageNav, languageinit } = this.state;
    return (
      <div
        className={`select-language treeNodeUnselectable ${languageNav ? 'act-select' : ''}`}
        onClick={this.selectLanguage}>
        <Icon style={{ fontSize: '22px' }} type="global" />
        <span className='language'>{languageinit.language}</span>
        <Icon className='B5C0D2' type="caret-down" />
        <div className='language-nav'>
          <ul>
            {
              languagelist.map((item, index) => {
                return (
                  <li key={item.id} onClick={(e) => { this.chooseLanguage(e, index) }}>{item.language}</li>
                )
              })
            }
          </ul>
        </div>
      </div>
    );
  }
}

export default Languageselect;
