import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';
import i18next from 'i18next';
import { inject, observer } from 'mobx-react';
import dayjs from 'dayjs';
import { Icon, message, Tooltip } from 'antd';
import Nodata from '../../common/Widget/Nodata';
import { getFile, trim, reg } from '../../util/util';
import { get, observable } from 'mobx';
import Loading from '../../common/Widget/Loading';


@withTranslation() @inject('playlist', 'bindframe') @observer
class Selectlist extends Component {
  constructor(props) {
    super(props);
    this.nameRef = React.createRef();
    this.creNameRef = React.createRef();
    this.state = {
      sortList: [
        { id: 1, text: 'updateSort' },
        { id: 2, text: 'creationSort' },
        { id: 3, text: 'photosSort' },
        { id: 4, text: 'nameSort' }
      ],
      defaultSort: {},
      sortindex: 0,
      sortDis: false,
      isdes: false,
      checklist: {}
    }
  }
  @observable list = null;
  @observable sortList = [
    { id: 1, text: 'updateSort' },
    { id: 2, text: 'creationSort' },
    { id: 3, text: 'photosSort' },
    { id: 4, text: 'nameSort' }
  ];
  @observable defaultSort = {};
  @observable sortindex = 0;
  @observable sortDis = false;
  @observable isdes = false;
  @observable checklist = {};
  flag = false;

  componentDidMount() {
    this.defaultSort = this.sortList[0];
    this.props.playlist.getplaylist(0, false, () => {
      if (this.props.farmedata.id) {
        let pos = this.props.playlist.bindplaylist.findIndex((el) => el.id === this.props.farmedata.id);
        this.checklist = this.props.playlist.bindplaylist[pos];
      }
    });
    if (this.props.playlist.bindplaylistinit && this.props.farmedata.id) {
      let pos = this.props.playlist.bindplaylist.findIndex((el) => el.id === this.props.farmedata.id);
      this.checklist = this.props.playlist.bindplaylist[pos];
    }

    document.body.addEventListener('click', this.hide)
  }
  componentWillUnmount() {
    this.props.playlist.framesbindcancel();
    document.body.removeEventListener('click', this.hide);
  }

  // 更多/排序modal隐藏
  hide = e => {
    this.sortDis = false;
  }

  pitchOn = (item) => {
    if (!this.props.playlist.flag) {
      this.checklist = item;
    }
  }

  //排序
  sortEldis = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    this.sortDis = !this.sortDis;
  }

  //点击排序
  changeIndex(i) {
    if (!this.props.playlist.flag) {
      if (this.sortindex === i) {
        this.isdes = !this.isdes;
      } else {
        this.sortindex = i;
      }
      this.defaultSort = this.sortList[i];
      this.props.playlist.sorting(i, this.isdes);
    }
  }

  //筛选
  filtration = () => {
    if (!this.props.playlist.flag) {
      const val = this.nameRef.current.value;
      this.props.playlist.filtration(val, this.sortindex, this.sortDis);
    } else {
      this.nameRef.current.value = '';
    }
  }

  //新建列表
  createdfbplay = () => {
    const val = trim(this.creNameRef.current.value);
    if (!val) {
      message.error(i18next.t('enterName'));
      return;
    }
    if (!reg.playlistname.test(val)) {
      message.error(i18next.t('Listnamedoes'));
      return;
    }
    this.props.playlist.creframesbindplay(val);
  }

  createPlaylist = () => {
    this.checklist = {};
    if (!this.props.playlist.flag) {
      this.props.playlist.framesbindcreplay();
    }
  }

  close_cre = () => {
    this.props.playlist.close_cre();
  }

  onOk = () => {
    if (!this.checklist.id) {
      message.error(i18next.t('Noselected'));
      return;
    }
    this.props.handleOk(this.checklist)
  }

  cancel = () => {
    this.props.playlist.close_cre();
    this.props.cancel();
  }


  render() {
    const { t } = this.props;
    const { sortList, defaultSort, isdes, sortDis, checklist } = this;
    const {
      bindplaylistinit,
      bindplaylist } = this.props.playlist;
    let playel;
    if (bindplaylistinit) {
      if (bindplaylist.length > 0) {
        playel = bindplaylist.map(item => {
          if (get(item, 'name')) {
            return (
              <div
                className={`list-box ${item.id === checklist.id ? 'list-checked' : ''}`}
                key={item.id}
                onClick={() => { this.pitchOn(item) }}>
                <span
                  className='list-box-check'>
                  <Icon type="check" />
                </span>
                <div className='top-img'>
                  <div className="img1">
                    {item.profile && <img alt={item.profile.key} src={getFile.file_s(item.profile.id)} />}
                  </div>
                  <div className="img2-4">
                    <span className="img2">
                      {item.profile_1 && <img alt={item.profile_1.key} src={getFile.file_s(item.profile_1.id)} />}
                    </span>
                    <span className="img3">
                      {item.profile_2 && <img alt={item.profile_2.key} src={getFile.file_s(item.profile_2.id)} />}
                    </span>
                    <span className="img4">
                      {item.profile_3 && <img alt={item.profile_3.key} src={getFile.file_s(item.profile_3.id)} />}
                    </span>
                  </div>
                </div>
                <div className='bottom-text'>
                  <Tooltip placement="bottom" overlayClassName='tooltip' title={get(item, 'name')}>
                    <p className='playlist-name'>{item.name}</p>
                  </Tooltip>
                  <p className='update-time'>
                    <span>{`${t('LastUpdate')}:`}{dayjs(item.updated).format(t('L'))}</span>
                  </p>
                  <span className='playlist-photo-num'>{t('Photo')}:{item.filecount ? item.filecount : 0}</span>
                </div>
              </div>
            )
          } else {
            return (
              <div className='cre-list-box' key={item.id}>
                <span className='cre-list-close' onClick={this.close_cre}>
                  <Icon type="close" />
                </span>
                <div className='cre-top-img'>
                  <Icon type="profile" />
                </div>
                <div className='cre-bottom-input'>
                  <input ref={this.creNameRef} maxLength={50} autoFocus type='text' placeholder={t('NewPlaylist')} />
                  <span onClick={this.createdfbplay}>
                    <Icon type="check" />
                  </span>
                </div>
              </div>
            )
          }
        })
      } else {
        playel = <Nodata />
      }
    } else {
      playel = <Loading />
    }



    return (
      <div className='module-canvas'>
        <div className='Selectlist-inner'>
          <div className='Selectlist-head'>
            <span>{t('DefaultPlaylist')}</span>
            <span onClick={this.cancel}><Icon type='close' /></span>
          </div>
          {bindplaylistinit && <div className='action-bar clearfix'>
            <div className='Selectlist-sort'>
              <span className='sort' onClick={(e) => { this.sortEldis(e) }}>{t(defaultSort.text)}{isdes ? <Icon type="arrow-up" /> : <Icon type="arrow-down" />}</span>
              <div className='sort-list' style={sortDis ? { display: 'block' } : { display: 'none' }}>
                <ul>
                  {
                    sortList.map((item, index) => {
                      return (
                        <li key={item.id} onClick={() => { this.changeIndex(index) }}>
                          <span>
                            <Icon type="check" className={item.id === defaultSort.id ? 'check-icon' : null} />
                            {t(item.text)}</span>
                        </li>
                      )
                    })
                  }
                </ul>
              </div>
            </div>
            <div className='Selectlist-filtrate'>
              <input ref={this.nameRef} type="text" placeholder={t('Screen')} onInput={this.filtration} />
              <Icon type="filter" />
            </div>
          </div>}
          <div className='Selectlist-content-list'>
            {playel}
          </div>
          <div className='Selectlist-foot clearfix'>
            <div className='create-playlist-btn' onClick={this.createPlaylist}>
              <Icon type="plus" />
              <span>{t('NewPlaylist')}</span>
            </div>
            <div className='Selectlist-btns'>
              <span className='cancel' onClick={this.cancel}>{t('Cancel')}</span>
              <span className='affirm' onClick={this.onOk}>{t('Determine')}</span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Selectlist;