import React, { Component } from 'react';
import { Progress, notification, Icon, Modal } from 'antd';
import { withTranslation } from 'react-i18next';
import { inject, observer } from 'mobx-react';
import './style.scss';
import event from '../../util/event';
import { observable } from 'mobx';
import i18next from 'i18next';
import { color } from '../../progresscolor'

const { confirm } = Modal;

@withTranslation() @inject('uploadFiles') @observer
class Uploadprogress extends Component {
  @observable winNum = 0;
  @observable failUpload = 0;

  componentDidMount() {
    event.on('limit', (res) => {
      if (res > 0) {
        notification.warning({
          message: this.props.t('limit'),
          description:
            `${this.props.t('filtered')} ${res} ${this.props.t('file')}`,
        });
      }
    });

  }

  componentWillUnmount() {
    event.rmName('UploadComplete');
    event.rmName('limit');
  }

  close = () => {
    let arr = this.props.uploadFiles.percentsArr.filter(item => !item.success);
    if (arr.length > 0) {
      confirm({
        title: i18next.t('Discardupload'),
        content: i18next.t('unfinishedfiles'),
        okText: i18next.t('Determine'),
        okType: 'danger',
        cancelText: i18next.t('Cancel'),
        onOk: () => {
          this.props.uploadFiles.abandon();
        },
        onCancel() { },
      });
    } else {
      this.props.uploadFiles.modalHide();
    }
  }

  render() {
    const { t } = this.props;
    const { visible,
      dis,
      is_dis,
      percentsArr,
      winTotalNum,
      finish,
      failUpload } = this.props.uploadFiles;
    return (
      <div id='Uploadprogress' style={visible ? {} : { transform: 'translateY(-110px)' }}>
        <div className='progress-header'>
          <div className='progress-title'>
            <p>{finish ? `${t('Successuploaded')} ${winTotalNum} ${t('individual')}` : t('Uploadcompleted')}</p>
            <p>{failUpload > 0 && `${t('fail')} ${failUpload} ${t('individual')}`}</p>

            {/* <p>{`${t('Successuploaded')} ${winTotalNum} ${t('individual')}`}{failUpload > 0 && ` ,${t('fail')} ${failUpload} ${t('individual')}`}</p> */}
          </div>
          <div className='progress-operation'>
            <Icon onClick={is_dis} style={dis ? { transform: 'rotate(180deg)' } : {}} type="down" />
            <Icon onClick={this.close} type="close" />
          </div>
        </div>
        <div className={`progress-boby  ${dis && visible ? 'block' : ''}`} >
          <div className='progress-list'>
            <ul>
              {
                percentsArr.map((item) => {
                  if (item.success) {
                    return null;
                  } else {
                    return (
                      <li key={item.id}>
                        <Icon type="picture" theme="filled" />
                        <span className='filename'>{item.file.name}</span>
                        <div className='right'>
                          {item.delete && <Icon onClick={() => { this.props.uploadFiles.delete(item) }} className='cancelup' type='close' />}

                          {item.invalid && item.awaitUp && <span className='hashErr'>{t('Invalidpicture')}</span>}
                          {item.unsuccessfully && <span className='retry' onClick={() => { this.props.uploadFiles.retry(item) }}>{t('retry')}</span>}
                          {item.invalid ? null : <span className='awaitUp'>{item.awaitUp ? `${t('uploading2')}...` : `${t('waiting')}...`}</span>}

                          <Progress
                            className='list-progress'
                            trailColor='#C5C5C5'
                            strokeColor={color}
                            strokeWidth={17}
                            showInfo={false}
                            type="circle"
                            percent={item.percent}
                            width={24} />
                        </div>
                      </li>
                    )
                  }
                })
              }
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default Uploadprogress;