import { observable, action } from "mobx";
import { wsget, host } from '../../ws';
import { ajaxPromise, changeHash,getCookie } from '../../util/util';
import event from "../../util/event";
import { Modal } from "antd";

const getIdInit = function(id){
  return function (){
      return id++;
  }
}

let getId = getIdInit(1);

const interval = 8000;

class uploadFiles {
  @observable visible = false; //上传列表显隐标识
  @observable percentsArr = []; //文件列表
  @observable winTotalNum = 0; //成功上传数量
  @observable failUpload = 0; //失败个数
  @observable dis = true; //上传进度列表显隐标识
  startTime = null;
  finishTime = null;
  timeInterval = null;
  compFlag = false; //是否压缩标记
  interrupt = false; //全部清除标识
  updated = null; //更新时间
  finish = false; //是否正在上传
  noUploads = 0; //超出限制的文件个数
  num = 0;
  initial = 0; //
  idx = 0; //
  fristDo = true;
  hashArr = [];
  albumArr = [];
  palyArr = [];
  idList = [];
  palySaveErr = []; //保存列表连接断开缓存数据

  //上传进度显示
  @action modalShow = () => {
    this.visible = true;
  }
  //上传进度隐藏
  @action modalHide = () => {
    this.visible = false;
    event.rm(this.time);
    event.rm(this.connect);
  }
  //缩小
  @action is_dis = () => {
    this.dis = !this.dis;
  }
  //清除全部
  @action abandon = () => {
    clearTimeout(this.timeInterval);
    this.saveDatabase(1);
    this.interrupt = true;
    this.finish = false;
    this.visible = false;
    event.rm(this.time);
    event.rm(this.connect);
  }

  //文件处理
  @action upFile = (e, id, reset, updated) => {
    this.noUploads = 0;
    let filelist = e.target.files;
    if (this.interrupt) {
      this.percentsArr = [];
      this.idx = 0;
      this.num = 0;
      this.failUpload = 0;
      this.fristDo = true;
      this.compFlag = false;
    }
    if(!this.finish){
      this.winTotalNum = 0;
    }
    this.interrupt = false;
    if (filelist.length > 0) {
      this.dis = true;
      this.finish = true;
      if (!this.idList.find(item => item === id)) {
        this.idList.push(id);
      }
      for (let value of filelist) {
        if (/image\/\w+/.test(value.type)) {
          if (value.size > 10485760) {
            this.noUploads++;
            continue;
          }
        }else{
          if (value.size > 52428800) {
            this.noUploads++;
            continue;
          }
        }
        let fileData = {
          id: `${getId()}`,
          to: id,
          file: value,
          percent: 0,
          awaitUp: false,
          unsuccessfully: false,
          invalid: false,
          delete: true,
          hashdata: null,
          success: false
        }
        if (!updated) {
          fileData.root = 'album';
        }
        this.percentsArr.push(fileData);
      }

      event.emit('limit', this.noUploads);
      this.startTime = +new Date();
      this.batchSave();

      this.visible = true;

      if (!this.compFlag) {
        this.up();
      }
      reset();

      this.time = event.on('updatetime', (time) => {
        this.updated = time;
      })

      this.connect = event.on('wsinit', async (res) => {
        if (res === 1) {
          console.log('连接上了');
          this.batchSave();

          if (this.hash) {
            this.hashtest(this.hash);
            this.hash = null;
          }

          if (this.palySaveErr.length > 0) {
            let arr = [];
            this.palySaveErr.forEach((item) => {
              arr = item.newarr;
              wsget('playlist_save_to', item.data).then((res) => {
                console.log('playlist_save_to  响应成功》》》》》》', res);
                event.emit('playupload', { res, flag: item.flag });
              })
            })
            this.palyArr.splice(0, arr.length);
          }
        }
      })
    }
  }

  //开始上传
  @action up = async () => {
    this.compFlag = true;
    let item = this.percentsArr[this.idx];
    try {
      let hashdata = await changeHash(item, { quality: 0.7 }, 2048, 2048);
      item.hashdata = hashdata;
      if (this.fristDo) {
        item.awaitUp = true;
        item.delete = false;
        this.hashtest(item);
        this.fristDo = false;
      }
      this.idx++;
      if (this.percentsArr[this.idx]) {
        if (!this.interrupt) {
          this.up();
        }
      } else {
        console.log('压缩完了');
        this.compFlag = false;
      }
    } catch (error) {
      item.hasherr = true;
      item.hashdata = true;
      if (this.fristDo) {
        item.awaitUp = true;
        item.delete = false;
        this.hashtest(item);
        this.fristDo = false;
      }
      this.idx++;
      if (this.percentsArr[this.idx]) {
        if (!this.interrupt) {
          this.up();
        }
      } else {
        console.log('压缩完了');
        this.compFlag = false;
      }
    }
  }

  //hash验证
  @action hashtest = (file) => {
    console.log('hash验证发出去了');
    if (!file.success) {
      if (!file.hasherr) {
        if (window.ws.isconnected()) {
          wsget('file_check_hash', file.hashdata.data.hash).then((data) => {
            if (!this.interrupt) {
              console.log('hash验证回来了', data);
              if (data.id === 0) {
                //不存在
                let url = `${host()}/up/p/${getCookie('cookie')}/${file.hashdata.data.hash}/${file.file.name}`;
                if (/video\/\w+/.test(file.file.type)) {
                  url = `${host()}/up/v/${getCookie('cookie')}/${file.hashdata.data.hash}/${file.file.name}`;
                }
                this.postUp(file, url)
              } else if (data.id > 0) {
                //存在
                const fileObj = {
                  to: file.to,
                  data: [{
                    id: data.id,
                  }]
                };
                if (data.ext) {
                  fileObj.data[0].ext = data.ext;
                }
                if (file.root) {
                  fileObj.root = file.root;
                }
                this.savefile(fileObj);
                this.rmList(file.id);
                if (!file.retry) {
                  this.continue(file.id);
                }
              } else {
                //空间不足
                Modal.error({
                  title: 'Insufficient cloud space',
                  content: 'Insufficient cloud space',
                });
              }
            }
          }).catch((err) => {
            console.log('hasherr---------', err);
            if (window.ws.isconnected()) {
              if (err.state === 125) {
                this.hashtest(file);
              } else {
                if (!this.hash) {
                  this.hash = file;
                }
              }
            } else {
              if (!this.hash) {
                this.hash = file;
              }
            }
          })
        } else {
          console.log('连接断开了');
          this.hash = file;
        }
      } else {
        // this.continue(file.id);
        file.awaitUp = true;
        file.invalid = true;
        file.delete = true;
        this.failUpload += 1;
        this.continue(file.id);
      }
    } else {
      this.continue(file.id);
    }
  }

  //上传图片
  @action postUp = (item, url) => {
    ajaxPromise(item.hashdata.data.blob, url, this.uploadProgress, item.id).then((result) => {
      if (!this.interrupt) {
        const fileObj = {
          id: result.fileid,
          to: item.to,
          data: [{
            id: result.data.id
          }]
        };
        if (result.data.ext) {
          fileObj.data[0].ext = result.data.ext;
        }
        if (item.root) {
          fileObj.root = item.root;
        }
        this.savefile(fileObj);
        this.rmList(result.fileid);
        if (!item.retry) {
          this.continue(result.fileid)
        }
      }
    }).catch((err) => {
      console.log(err, '上传出错‘’‘’‘’‘’‘’‘’‘’');
      this.error(item);
    });
  }

  //上传进度监听
  @action uploadProgress = (ev, id) => {
    if (ev.lengthComputable) {
      const item = this.percentsArr.find(item => item.id === id);
      let precent = Math.floor(ev.loaded / ev.total * 100);
      item.percent = precent;
    }
  }

  //上传成功删除
  @action rmList = (id) => {
    const item = this.percentsArr.find(item => item.id === id);
    item.success = true;
    if (item.retry) {
      this.failUpload -= 1;
    }
    this.winTotalNum += 1;
  }

  //保存文件
  @action savefile = (data) => {
    if (data.root) { //云相册上传
      let obj = {
        id: data.to,
        list: data.data
      }
      wsget('album_add_files', obj);
      event.emit('albumupload', { data: obj.list, id: obj.id });
    } else {
      let obj = {
        id: data.to,
        list: data.data
      }
      this.palyArr.push(obj);
      event.emit('savefiles', data);
    }
  }

  //继续下一个上传
  @action continue = async (id) => {
    if (!this.interrupt) {
      const pos = this.percentsArr.findIndex(item => item.id === id);
      if (this.percentsArr[pos + 1]) {
        let item = this.percentsArr[pos + 1];
        if (item.hashdata) {
          item.awaitUp = true;
          item.delete = false;
          this.hashtest(item);
        } else {
          console.log('utttttttttttttttttt');
          item.awaitUp = true;
          item.delete = false;
          try {
            let hashdata = await changeHash(item, { quality: 0.7 }, 2048, 2048);
            item.hashdata = hashdata;
          } catch (error) {
            item.hasherr = true;
            item.hashdata = true;
          }

          this.hashtest(item);
        }
      } else {
        console.log('‘没有了');
        // this.saveDatabase();
        this.finish = false;
        this.dis = false;
        this.fristDo = true;
        this.finishTime = +new Date();
        if (this.finishTime - this.startTime < interval) {
          clearTimeout(this.timeInterval);
          this.saveDatabase(1);
        }
      }
    }
  }

  //重试上传
  @action retry = async (data) => {
    const item = this.percentsArr.find(item => item.id === data.id);
    item.percent = 0;
    item.awaitUp = true;
    item.delete = false;
    item.invalid = false;
    item.unsuccessfully = false;
    item.retry = true;
    let hashdata = await changeHash(item, { quality: 0.7 }, 2048, 2048);
    item.hashdata = hashdata;
    this.hashtest(item);
  }


  //删除上传
  @action delete = (data) => {
    const item = this.percentsArr.find(item => item.id === data.id);
    if (item.invalid || item.unsuccessfully) {
      this.failUpload -= 1;
    }
    item.success = true;
  }

  //保存列表
  @action saveDatabase = (flag) => {
    if (this.palyArr.length > 0) {
      let newarr = this.palyArr.slice();
      let obj = {};
      this.idList.forEach(val => {
        obj[`${val}`] = newarr.reverse().filter(item => item.id === val);
      })
      for (let k in obj) {
        let arr = [];
        obj[k].forEach(item => {
          arr.push(item.list[0]);
        })
        let data = {
          id: Number(k),
          list: arr,
          index: 0
        }
        if (arr.length > 0) {
          if (window.ws.isconnected()) {
            wsget('playlist_save_to', data).then((res) => {
              console.log('playlist_save_to  响应成功》》》》》》', res);
              event.emit('playupload', { res, id: data.id, flag });
            })
          } else {
            this.palySaveErr.push({
              data, newarr, flag
            });
          }
        }
      }
      if (window.ws.isconnected()) {
        this.palyArr.splice(0, newarr.length);
      }
    }
  }

  @action batchSave = () => {
    this.timeInterval = setInterval(() => {
      this.startTime = +new Date();
      if (window.ws.isconnected()) {
        this.saveDatabase();
      }
    }, interval);
  }

  //失败
  @action error = (data) => {
    if (!this.interrupt) {
      data.awaitUp = false;
      data.invalid = true;
      data.delete = true;
      data.unsuccessfully = true;
      if (!data.retry) {
        this.failUpload += 1;
      }
      if (!data.retry) {
        this.continue(data.id);
      }

    }
  }

  @action clearData = () => {
    this.visible = false; //上传列表显隐标识
    this.percentsArr = []; //文件列表
    this.winTotalNum = 0; //成功上传数量
    this.failUpload = 0; //失败个数
    this.dis = true; //上传进度列表显隐标识
    this.startTime = null;
    this.finishTime = null;
    this.timeInterval = null;
    this.compFlag = false; //是否压缩标记
    this.interrupt = false; //全部清除标识
    this.updated = null; //更新时间
    this.finish = false; //是否正在上传
    this.noUploads = 0; //超出限制的文件个数
    this.num = 0;
    this.initial = 0; //
    this.idx = 0; //
    this.fristDo = true;
    this.hashArr = [];
    this.albumArr = [];
    this.palyArr = [];
    this.idList = [];
    this.palySaveErr = []; //保存列表连接断开缓存数据
  }

}

export default new uploadFiles();






















